import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ApiGeneralService } from 'src/app/services/api-general.service';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { ToastService } from 'src/app/services/toast.service';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-vlogs',
  templateUrl: './vlogs.page.html',
  styleUrls: ['./vlogs.page.scss'],
})
export class VlogsPage implements OnInit {

  arrVlogs = [];
  iframe: any;
  isVideoOpen:boolean;
  AssetsPathVideo = environment.webAssetsPath + "mobility-app/videothumbnails";

  constructor(
    private headerTitle: HeaderTitleService,
    private apiGeneralService : ApiGeneralService,
    private toastr : ToastService,
    private sanitizer: DomSanitizer,
  ) { 
    this.headerTitle.title="Vlog Series";
  }

  ngOnInit() {
    this.getVlogs();
  }

  getVlogs() {
    let postData = { 'videoHeaderId': 38, 'isActive': true }
    this.apiGeneralService.getVideos(JSON.stringify(postData)).then(resp => {
      this.arrVlogs = resp;
    }).catch(err => {
      console.log("getVlogs: " + err);
    })
  }

  playVideo(videoSrc) {
    if (videoSrc == "") {
      this.toastr.toastError("Oops! Video is not available.");
      return false;
    }
    this.iframe = this.sanitizer.bypassSecurityTrustHtml('<iframe src="' + videoSrc + '" frameborder="0" style="width: 100%; height: 100%;"allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>');    
    this.isVideoOpen = true;
  }
  videoPanelClose() {
    this.isVideoOpen = false;
    this.iframe = "";
  }
}
